.c-profile__tabs {
  border-bottom: 0px !important;
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  > a {
    justify-content: stretch;
    flex-grow: 10;
    border-radius: 15px 15px 0 0;
    padding: 15px;
    text-align: center;
  }
}
.nav-tabs .nav-link {
  background-color: #fff;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  padding: $base-unit-x;
  border: none !important;
}
.c-profile_tab-content {
  height: auto;
  background: #fff;
  padding-bottom: $base-unit-x;
  border-radius: 0 0px $base-unit-2x $base-unit-2x;
  .c-box {
    border: 1px solid $gray-color;
  }
}
.c-profile__sign-profile-number {
  color: #007bff !important;
}
.c-profile__card-content {
  height: auto;
  border: 2px solid #d7d7d7;
  border-radius: $base-unit-2x;
  padding: 5px;
  background-color: #fff;
  position: relative;
  z-index: 5;
}
.c-profile__sign-profile {
  border: 1px solid #d7d7d7;
  border-radius: $base-unit-x;
  background: #f8f8f8;
}
.c-profile_continue {
  color: #1cb2d9;
}
.c-profile_plus {
  color: #1cb2d9 !important;
  position: absolute;
  left: 30px;
  top: 2px;
  font-size: 30px;
}
.c-profile-plus {
  color: #007bff;
  font-size: $base-unit-2x;
  background: #f8f8f8;
}
.c-profile__checkbox {
  background: #f8f8f8;
  width: 80% !important;
  margin: auto;
}
.c-profile__img-profile {
  position: relative;
  bottom: 15px;
  right: $base-unit-x;
}
.c-profile__img-number {
  position: relative;
  position: relative;
  bottom: 9px;
  width: 30px;
  height: 30px;
  object-fit: contain;
  margin-left: 10px;
}
.profile_challeng-text {
  margin-right: $base-unit-6x !important;
}
.c-profile__effect-week {
  // margin-right: $base-unit-7x !important;
  // margin-right: 105px !important;
}
