.c-profile__effects {
  border: 1px solid #cbcbcb;
  // padding: $base-unit-2x !important;
}
.table th,
.table td {
  vertical-align: middle;
}
.c-profile-side-information {
  width: 100% !important;
  display: flex;
  justify-content: space-around;
}

.c-profile__map-holder {
  // background-image: url('./assets/img/Profiles/mapRoad.png');
  padding: 40px 20px;
  margin: 0;
  position: relative !important;
  perspective: 150px;
  // background-size: cover;
  // background-repeat: repeat-y;
  // background-position: center;
  &:after {
    background-image: url('./assets/img/Profiles/mapRoad.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
    padding: 20px 20px !important;
    content: '';
    display: block;
    width: 100%;
    height: 110vh;
    background-color: rgba(0, 0, 0, 0.02);
    top: 0;
    left: 0;
    z-index: 1;
    transform-style: preserve-3d;
    transform: rotateX(1deg);
    border: 8px solid rgba(0, 0, 0, 0.04);
  }
  &:before {
    position: absolute;
    content: '';
    display: block;
    width: 104%;
    height: 40px;
    z-index: 2;
    border: 8px solid rgba(0, 0, 0, 0.04);
    bottom: -45px;
    left: -2%;
    background-color: rgba(0, 0, 0, 0.07);
  }
}

