/* ==========================================================================
   #MODALS
   ========================================================================== */


/**
 * This is a component. Extend inuitcss by building your own components
 * that make up your UI. Component classes are prefixed with a `c-`.
 */

.ReactModalPortal {
    position: relative;
    z-index: 9999;
}

.ReactModal__Overlay {
    background-color: rgba(244, 245, 249, .8) !important;
}

.ReactModal__Content {
    width: 800px;
    border: none !important;
    border-radius: 15px !important;
}

.c-modal__mini {
    width: 400px;
    margin: 0 auto;
}

.c-modal {}

.c-modal__symbol {
    height: 120px;
    width: auto;
    display: block;
}
.c-modal__title {
    font-size: 24px;
    line-height: 1.5;
    padding: 25px 20px;
    font-weight: bold;
    border: 2px solid $border-color;
    border-radius: $global-radius;
    margin: 0;
    ::before {
        width: 0;
        height: 0;
        border-right: 60px solid #2E8DD0;
        border-bottom: 30px solid transparent;
        border-top: 30px solid transparent;
    }
}

.c-modal__title::before {
    width: 0;
    height: 0;
    border-right: 60px solid #2E8DD0;
    border-bottom: 30px solid transparent;
    border-top: 30px solid transparent;
}
.c-modal_moderator-content{
    border:2px solid #b0b0b0;
    width: 50%;
    margin: auto;
    border-radius: 20px;
}
.c-modal__close-modaretors{
    position: absolute;
    left:29px !important;
    color:#b0b0b0;
}
.c-modal__search {
    width: 100%;
    padding: 0;
    margin: 0;
    position: relative;
    input {
        width: 100%;
        padding: 15px 15px;
        border: 2px solid $dark-blue-color;
        appearance: none;
        -webkit-appearance: none;
        background-color: #fff;
        border-radius: 50px;
    }
    button {
        appearance: none;
        -webkit-appearance: none;
        position: absolute;
        left: 10px;
        top: 10px;
        font-size: 24px;
        color: rgba($dark-blue-color, .9)
    }
}
.c-modal__moderator-radio{
    // border-: 1px solid #b0b0b0 !important;
    border-top: 1px solid #b0b0b0 !important;
    border-bottom: 1px solid #b0b0b0 !important;
    border-left: 3px solid #b0b0b0 !important;
    border-right: 3px solid #b0b0b0 !important;
    border-radius: 10px;
    padding: 10px 2px;

}
.c-modal__moderator-radio2{
    border-top: 1px solid #b0b0b0 !important;
    border-bottom: 1px solid #b0b0b0 !important;
    border-left: 3px solid #b0b0b0 !important;
    border-right: 3px solid #b0b0b0 !important;
    border-radius: 10px;
    padding: 12px 5px; 
}
.c-modal_moderator-link{
    font-size:13px !important;
}
/* Mobile & Tablet
    ========================================================================== */

@media(max-width:1023px) {}

@media(max-width:650px) {
    .c-modal__symbol {
        width: 25% !important;
    }
    .c-modal__title {
        width: 40% !important;
    }
    .c-modal__title_step1 {
        font-size: 20px !important;
    }
    .c-modal__title_step2{
        margin: 0 auto !important;
        font-size: 20px !important;
    }
    .c-modal__login {
        width: 80% !important;
        margin: 0 auto !important ;
    }
    .c-modal__card{
        width: 46% !important;
        margin: 0 auto !important;
    }
    .c-scroll-modal{
        height: 100vh !important;
       overflow: scroll !important;
    }
}